import { Grid, Image, Link, List, ListItem, Stack, Text } from "@chakra-ui/react"
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, EffectFade, Navigation } from "swiper";
import 'swiper/css';

export const ProjectCard = ({ img, title, desc, ts, gLink, dLink }) => {
    return (
        <Stack className="project-card">
            <Swiper
                modules={[Autoplay, EffectFade, Navigation]}
                style={{ width: "500px" }}
                autoplay={{ delay: 2000, pauseOnMouseEnter:true, disableOnInteraction:false }}
                navigation
                loop
            >
                {img.map(e => {
                    return <SwiperSlide key={Math.random()}><Image src={e} alt={e} /></SwiperSlide>
                })}
            </Swiper>
            <Text className="project-title">{title}</Text>
            <Text className="project-description">{desc}</Text>
            <List className="project-tech-stack">{ts.map(e => {
                return <ListItem key={Math.random()}>{e}</ListItem>
            })}</List>
            <Grid templateColumns={"1fr 1fr"}>
                <Link target="_blank" href={gLink} className="project-github-link" >Github</Link>
                <Link target="_blank" href={dLink} className="project-deployed-link" >Deployed</Link>
            </Grid>
        </Stack >
    )
}