import { Image, Stack, Text } from "@chakra-ui/react"

export const SkillCard = ({ img, name }) => {
    return (
        <Stack className="skills-card">
            <Image className="skills-card-img" src={img} />
            <Text fontWeight={"medium"} fontSize={"xl"} className="skills-card-name">{name}</Text>
        </Stack>
    )
}